import React from 'react';
import { AboutImage, AboutText, AboutTextWrapper, AboutTitle, ContentWrapper, Wrapper } from './styledAbout';
import firstImg from './1.jpg'
import scndImg from './2.jpg'
import AnimatedElement from '../AnimatedElements';

export const About = () => {
    return (
        <Wrapper>
            <ContentWrapper>
                <AboutTextWrapper>
                    <AboutTitle>Paulownia: Nasza Pasja, Twoja Zielona Przyszo</AboutTitle>
                    <AboutText> Witaj na naszej stronie! Jestemy dumni, e moemy dostarczy Ci najwyszej jakoci sadzonki i drzewa Paulownia, ktre nie tylko ozdobi Twj ogrd, ale znajd zastosowanie na plantacjach drzew uytkowych. Nasze starannie wyselekcjonowane sadzonki s w zasigu Twojej rki! Gwarantujemy Ci profesjonalizm i pene zaangaowanie w to, co robimy. Paulownie to nasza pasja.
                    </AboutText>
                </AboutTextWrapper>
                <AboutImage src={firstImg} />
            </ContentWrapper>

            <ContentWrapper reverse>
                <AnimatedElement left>
                    <AboutImage src={scndImg} />
                </AnimatedElement>
                <AnimatedElement>
                    <AboutTextWrapper>
                        <AboutText> Nasza firma opiera si na dziesicioletnim dowiadczeniu, ktre zdobylimy dziki nieustannym prbom i bdom oraz podrom po wiecie, gdzie Paulownia wystpuje w rnorodnych klimatach i warunkach. To wanie te dowiadczenia sprawiy, e jestemy ekspertami w hodowli tych drzew. Nasz pasj jest dostarczanie Ci najlepszych sadzonek, aby Twoje ogrody i plantacje drzew mogy kwitn i rosn tak, jak nasze drzewa. Razem moemy kroczy w kierunku zielonej przyszoci!
                        </AboutText>
                    </AboutTextWrapper>
                </AnimatedElement>
            </ContentWrapper>

        </Wrapper>
    );
};

