import React from 'react';
import { Content, Header, HeaderTitle, Image, TD, THead, TH, Table, Tile, TileText, TileTextWrapper, TileTitle, TileWrapper, Wrapper, TBody, TablesWrapper, TableWrapper, Caption, Title, CalcLink, PriceWrapper } from './styledOffer';
import AnimatedTile from './AnimatedTile';
import seedling from './sadzonka.png';
import tree from './tree.png';

export const Offer = () => {

    return (
        <Wrapper>
            <Header>
                <HeaderTitle>Nasza oferta</HeaderTitle>
            </Header>
            <Content>
                <TileWrapper>
                    <Tile>
                        <TileTextWrapper>
                            <TileTitle>
                                Sadzonki
                            </TileTitle>
                            <TileText>
                                Nasza oferta zawiera sadzonki Paulowni, ktre s dostpne w rnych ilociach, idealne dla tych, ktrzy pragn wspiera rozwj polskiej produkcji drzew. Dostpne odmiany to Shang Tong i Tomentosa, ktre nie tylko zapewniaj szybki wzrost, ale rwnie s bardziej odporne na warunki atmosferyczne. To doskonay wybr dla tych, ktrzy kieruj si trosk o rodowisko i marz o dodaniu zieleni do swojego otoczenia. Nasze sadzonki to nie tylko inwestycja finansowa, ale take inwestycja w przyszo - poprawa jakoci powietrza wok nas, co ma istotne znaczenie dla naszego rodowiska. Zdecyduj si na zakup naszych sadzonek Paulowni, aby wesprze lokaln produkcj i cieszy si korzyciami ekologicznymi, jakie przynosi ta pikna rolina. Warto zaznaczy, e sadzonki wysiewamy sami w polskich warunkach z nasion, ktre pochodz z drzewa rosncego w Polsce. Dziki temu moemy zagwarantowa, e nasze drzewa s odpowiednio przystosowane do lokalnego klimatu, co sprzyja ich zdrowemu wzrostowi i rozwojowi.
                            </TileText>
                        </TileTextWrapper>
                        <Image src={seedling} />
                    </Tile>
                    <AnimatedTile>

                        <Tile left>
                            <Image treeSize src={tree} />
                            <TileTextWrapper>
                                <TileTitle>
                                    Drzewa
                                </TileTitle>
                                <TileText>
                                    Nasze drzewa Paulowni s dostpne w rnych wysokociach od 1,5 m do 4,5 m i stanowi doskona inwestycj w przyszo oraz ochron rodowiska. Odmiana Shang Tong jest atwa w uprawie, a gwn korzyci pync z zakupu drzewa jest szybsze uzyskanie zyskw w porwnaniu do sadzonki. Wybierajc drzewa Paulowni, podejmujesz decyzj na rzecz przyszoci i dobra naszej planety. Warto doda, e nasze drzewa s przez nas sadzone jako sadzonki, a nastpnie rosn w naszym klimacie przez okres dwch lat. Ten proces pozwala nam zagwarantowa, e drzewa s solidnie ukorzenione i przygotowane do szybkiego wzrostu, gdy zostan przeniesione do ostatecznej lokalizacji.
                                </TileText>
                            </TileTextWrapper>
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <PriceWrapper>
                            <Title>Cennik</Title>
                            <TablesWrapper>
                                <TableWrapper>
                                    <Caption>Cena drzewa wedug wysokoci</Caption>
                                    <Table>
                                        <THead>
                                            <tr>
                                                <TH>Od</TH>
                                                <TH>Do</TH>
                                                <TH>Cena za sztuk</TH>
                                            </tr>
                                        </THead>
                                        <TBody>
                                            <tr>
                                                <TD>50 cm</TD>
                                                <TD>100 cm</TD>
                                                <TD>70 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>100 cm</TD>
                                                <TD>150 cm</TD>
                                                <TD>100 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>150 cm</TD>
                                                <TD>200 cm</TD>
                                                <TD>150 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>200 cm</TD>
                                                <TD></TD>
                                                <TD>cena indywidualna</TD>
                                            </tr>
                                        </TBody>
                                    </Table>
                                </TableWrapper>

                                <TableWrapper>
                                    <Caption>Cena sadzonki wedug liczby</Caption>
                                    <Table>

                                        <THead>
                                            <tr>
                                                <TH>Od</TH>
                                                <TH>Do</TH>
                                                <TH>Cena za sztuk</TH>
                                            </tr>
                                        </THead>
                                        <TBody>
                                            <tr>
                                                <TD>1</TD>
                                                <TD>20</TD>
                                                <TD>40 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>21</TD>
                                                <TD>100</TD>
                                                <TD>35 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>101</TD>
                                                <TD>1000</TD>
                                                <TD>30 z</TD>
                                            </tr>
                                            <tr>
                                                <TD>1001</TD>
                                                <TD></TD>
                                                <TD>cena indywidualna</TD>
                                            </tr>
                                        </TBody>
                                    </Table>
                                </TableWrapper>
                            </TablesWrapper>
                            <CalcLink to="/Kalkulator" onClick={() => window.scrollTo(0, 0)} >Zaplanuj swoj plantacje</CalcLink>
                        </PriceWrapper>
                    </AnimatedTile>
                </TileWrapper>
            </Content>

        </Wrapper >
    );
};

