import React from 'react';
import { ContentWrapper, ServicesImage, ServicesText, ServicesTextWrapper, ServicesTitle, Wrapper } from './styledServices';
import firstImg from './3.jpg'
import scndImg from './4.jpg'
import AnimatedElement from '../AnimatedElements';

export const Services = () => {
    return (
        <Wrapper>
            <ContentWrapper>
                <AnimatedElement left >
                    <ServicesImage src={firstImg} />
                </AnimatedElement>

                <AnimatedElement>
                    <ServicesTextWrapper>
                        <ServicesTitle>Sadzonki</ServicesTitle>
                        <ServicesText>Oferujemy sadzonki Paulowni w rnych ilociach, idealne dla tych, ktrzy chc wesprze polsk produkcj drzew. Dostpne odmiany to Shang Tong i Tomentosa, zapewniajce szybki wzrost i produkcj tlenu. To doskonay wybr dla tych, ktrzy dbaj o rodowisko i chc doda zieleni do swojego otoczenia. Nasze sadzonki s wysiewane w polskich warunkach, z nasion pochodzcych z drzewa rosncego w Polsce, co gwarantuje, e s one odpowiednio przystosowane do lokalnego klimatu i terenu. Dziki temu moemy zapewni, e nasze drzewa rosn silnie i zdrowo, przyczyniajc si do zielonej przestrzeni w caej Polsce.
                        </ServicesText>
                    </ServicesTextWrapper>
                </AnimatedElement>

            </ContentWrapper>

            <ContentWrapper reverse>
                <AnimatedElement left >
                    <ServicesTextWrapper>
                        <ServicesTitle>Drzewa</ServicesTitle>
                        <ServicesText>Nasze drzewa Paulowni, oferowane w zakresie wysokoci od 1,5 m do 4,5 m, stanowi doskona inwestycj w przyszo. Odmiana Shang Tong, charakteryzujca si atwoci w uprawie i naturalnym piknem, pozwala doda zieleni do otoczenia, jednoczenie przyczyniajc si do poprawy jakoci powietrza i ochrony rodowiska. Warto podkreli, e nasze drzewa przechodz proces sadzenia w postaci modych rolin, ktre nastpnie przez dwa lata rosn w polskim klimacie.
                            Dziki temu zapewniamy, e rolina jest dobrze ukorzeniona, co powoduje prawidowy wzrost w nowej lokalizacji.</ServicesText>
                    </ServicesTextWrapper>
                </AnimatedElement>

                <AnimatedElement>
                    <ServicesImage tree src={scndImg} />
                </AnimatedElement>
            </ContentWrapper>
        </Wrapper>
    );
};

