import React from 'react';
import { AboutText, Content, Header, HeaderTitle, Image, NewLine, TextWrapper, Title, Wrapper } from './styledAbout';
import circleImage from './aboutImage.png';

export const About = () => {
    return (
        <Wrapper>
            <Header>
                <HeaderTitle>O nas</HeaderTitle>
            </Header>

            <Content>
                <Image src={circleImage} />

                <TextWrapper>
                    <Title>Polskie Paulownie</Title>
                    <AboutText>     Nasza pasja do przyrody i troska o nasz klimat skoniy nas do zaoenia wasnej hodowli drzew tlenowych, ktr prowadzimy od ponad 8 lat. To nie tylko nasze zajcie, ale rwnie rodzinnie spajajce przedsiwzicie, w ktrym uczestniczy caa nasza rodzina oraz wielu przyjaci. Nasza misja to nie tylko prowadzenie biznesu, ale take wpywanie pozytywnie na otaczajcy nas wiat.
                        Hodowla drzew tlenowych to dla nas nie tylko rdo utrzymania, ale przede wszystkim sposb na aktywne zaangaowanie si w ochron rodowiska. Drzewa te peni niezwykle istotn rol w ekosystemie, poniewa absorbuj dwutlenek wgla oraz produkuj tlen. Nasza hodowla opiera si na starannie wyselekcjonowanych odmianie paulownii, ktra charakteryzuj si du odpornoci na temperatur oraz szkodniki.
                        Codzienna praca na naszej hodowli jest zarwno wyzwaniem, jak i ogromn przyjemnoci. Wierzymy, e kade drzewo, ktre ronie dziki naszej hodowli, to kropla w morzu dziaa na rzecz lepszego jutra dla nas wszystkich. Nasza hodowla nie opiera si jedynie na zysku, ale na trosce o przysze pokolenia i stan naszej planety. Jestemy dumni, e nasze drzewa pomagaj zwikszy zielon powierzchni oraz zmniejszy emisj dwutlenku wgla.
                        <NewLine>Wsppracujemy rwnie z lokalnymi spoecznociami i szkoami, organizujc edukacyjne wycieczki na nasze tereny. Chcemy dzieli si wiedz na temat ekologii, przyrody i znaczenia drzew dla naszego rodowiska. To nasz drobny, ale istotny wkad w budowanie wiadomoci ekologicznej.
                        </NewLine>
                    </AboutText>
                </TextWrapper>
            </Content>

        </Wrapper>
    );
};

