import React from 'react';
import { Content, Header, HeaderTitle, Image, MainText, Span, TextWrapper, Tile, TileText, TileTextWrapper, TileTitle, TileWrapper, Wrapper } from './styledCultivation';
import ex from '../../common/Images/Example/bee.png';
import AnimatedTile from './AnimatedTile';
import planting from '../../common/Images/Cultivation/planting.png'
import desks from '../../common/Images/Cultivation/desks.png'
import pelets from '../../common/Images/Cultivation/pelets.png'
import gardening from '../../common/Images/Cultivation/gardening.png'
import science from '../../common/Images/Cultivation/science.png'
import town from '../../common/Images/Cultivation/town.png'
import city from '../../common/Images/Cultivation/city.png'
import bee from '../../common/Images/Cultivation/bee.png'

export const Cultivation = () => {

    return (
        <Wrapper>
            <Header>
                <HeaderTitle>Uprawa paulowni</HeaderTitle>
            </Header>
            <Content>

                <TextWrapper>
                    <MainText>
                        Chcemy si z Pastwem podzieli naszym dowiadczeniem w poniszych dziedzinach:
                    </MainText>
                </TextWrapper>

                <TileWrapper>

                    <AnimatedTile>
                        <Tile left>
                            <Image src={gardening} />
                            <TileTextWrapper>
                                <TileTitle>
                                    Ogrodnictwo
                                </TileTitle>
                                <TileText>
                                    Kiedy paulownia kwitnie to u wszystkich wzbudza zachwyt, mona patrze i patrze oraz czu prawdziwy powiew wiosny. Wrd kwiatw uwijaj si pszczoy, gwarno dyskutujc o wionie, ale kiedy kwiaty opadn, paulownia zamienia si w zielon kopu piknych lici. Ksztat jej kulisty pasuje do naszych polskich ogrodw. Od wielu ju lat spotykamy j pojedynczych nasyceniach, ale take w alejach przy uzdrowiskach, miastach czy te szpitalach. Drzewo mona formowa, podnosi koron lub wysmukla dostosowujc do miejsca, w ktrym przyszo jej rosn. Paulownia ma due licie i kiedy przychodzi pierwszy przymrozek, wwczas licie czerniej w jedn noc i spadaj. Z racji swej cikoci nie fruwaj do ssiadw, ale pod swym konarem rozkruszaj si do marca, uyniajc tym samym gleb.                                </TileText>
                            </TileTextWrapper>
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile>
                            <TileTextWrapper>
                                <TileTitle>
                                    Plantacje na drewno uytkowe
                                </TileTitle>
                                <TileText>
                                    Paulownie to szybko rosnce drzewa, ktre s coraz czciej sadzone ze wzgldu na ich drewno uytkowe. Drewno paulowni jest lekkie i trwae, co czyni je atrakcyjnym surowcem do produkcji mebli, podg i innych wyrobw stolarskich. Ponadto, paulownie s rwnie cenne ze wzgldu na swoje waciwoci ekologiczne, poniewa s efektywne w pochanianiu dwutlenku wgla z atmosfery. To sprawia, e s one interesujcym wyborem dla osb i firm zainteresowanych zrwnowaonym gospodarowaniem lasami i produkcj drewna.                                </TileText>
                            </TileTextWrapper>
                            <Image src={desks} />
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile left>
                            <Image src={pelets} />
                            <TileTextWrapper>
                                <TileTitle>
                                    Produkcja Biomasy
                                </TileTitle>
                                <TileText>
                                    Paulownia jest jednym z najefektywniejszych gatunkw drzew szybko rosncych, co sprawia, e jest idealna do uprawy w celu produkcji biomasy. Jej drewno jest wyjtkowo lekkie i ma stosunkowo wysok warto energetyczn. Drzewa Paulowni mog by hodowane na plantacjach, a nastpnie przetwarzane na pellet, brykiety lub drewno opaowe, co przyczynia si do produkcji odnawialnej energii oraz biomasy wykorzystywanej w przemyle. To zrwnowaone rdo biopaliwa, ktre ma potencja do zaspokajania rosncego zapotrzebowania na energi.
                                </TileText>
                            </TileTextWrapper>
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile >
                            <TileTextWrapper>
                                <TileTitle>
                                    Rekultywacja Terenw
                                </TileTitle>
                                <TileText>
                                    Drzewa Paulowni stanowi doskona opcj dla rekultywacji terenw, ktre ulegy degradacji na skutek wydobycia surowcw naturalnych lub innych dziaalnoci przemysowych. Ich szybki wzrost i zdolno do dostosowywania si do rnych warunkw klimatycznych i glebowych sprawiaj, e s idealnym wyborem. Korzenie Paulowni pomagaj w stabilizacji gruntw i zapobiegaj erozji. W wyniku tego procesu, tereny, ktre kiedy byy nieuyteczne, staj si przyjazne dla przyrody, co przyczynia si do odbudowy ekosystemw i przywracania naturalnej rwnowagi.                                </TileText>
                            </TileTextWrapper>
                            <Image src={planting} />

                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile left>
                            <Image src={bee} />
                            <TileTextWrapper>
                                <TileTitle>
                                    Pszczelarstwo
                                </TileTitle>
                                <TileText>
                                    Rolina naley do gatunku rolin C4  miododajnych. Od koca kwietnia, przez 5 tygodni, kwitnie w koszykach kwiatowych o wysokoci do 60 cm. Z kocem maja kwiaty schn i pojawiaj si licie. Najbardziej znana jest jako paulownia puszysta, ale mona j spotka take pod nazw: cesarska lub omszona. Wieczorem, gdy pszczoy ju nie pracuj, sycha jeszcze gwar trzmieli, ktre zbieraj spad. Z racji, e licie s lepkie, nie spotykamy pod tymi drzewami mszyc, komarw, muszek  chyba boj si, e si przyklej. W Moszczanicy obok ywca wraz z uczniami klasy pszczelarskiej moemy obserwowa cay rok jak pszczoy wsppracuj z paulowni.
                                </TileText>
                            </TileTextWrapper>
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile >
                            <TileTextWrapper>
                                <TileTitle>
                                    Sadzenie dla Krajobrazu Miejskiego
                                </TileTitle>
                                <TileText>
                                    Paulownia jest doskonaym dodatkiem do miejskich krajobrazw, ktry przynosi nie tylko estetyczne korzyci, ale take wpywa na jako powietrza w obszarach miejskich. Jej pikne, fioletowe kwiaty i dekoracyjne licie czyni j atrakcyjnym drzewem dla miejskich ogrodw i parkw. Ponadto, drzewa te przyczyniaj si do oczyszczania powietrza z zanieczyszcze i dostarczaj cennego cienia w cieplejszych miesicach.     </TileText>
                            </TileTextWrapper>
                            <Image src={city} />
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile left>
                            <Image src={town} />
                            <TileTextWrapper>
                                <TileTitle>
                                    Hodowla Zwierzt
                                </TileTitle>
                                <TileText>
                                    Drzewa Paulowni oferuj cenny cie i schronienie dla zwierzt hodowlanych w okresach gorcych letnich miesicy. To gatunek drzewa, ktry ronie gsto i zapewnia przyjemne warunki do wypasu i wypasu zwierzt. Dla rolnikw, ktrzy dbaj o swoje stada, Paulownia stanowi naturalny sposb na utrzymanie zwierzt w komfortowych warunkach i poprawienie ich samopoczucia.      </TileText>
                            </TileTextWrapper>
                        </Tile>
                    </AnimatedTile>

                    <AnimatedTile>
                        <Tile>
                            <TileTextWrapper>
                                <TileTitle>
                                    Badania Naukowe
                                </TileTitle>
                                <TileText>
                                    Paulownia jest przedmiotem licznych bada naukowych zwizanym z jej upraw, waciwociami drewna i wpywem na rodowisko. Badacze interesuj si szczeglnie potencjaem tych drzew do sekwestroacji dwutlenku wgla, produkcji biomasy, oraz jako rda biomasy dla celw energetycznych i przemysowych. Dziki swojemu szybkiemu wzrostowi i wszechstronnoci, Paulownia przyciga uwag naukowcw dcych do zrwnowaonego rozwoju.  </TileText>
                            </TileTextWrapper>
                            <Image examination src={science} />
                        </Tile>
                    </AnimatedTile>


                </TileWrapper>
            </Content>

        </Wrapper >
    );
};

